import {COMMA, ENTER} from '@angular/cdk/keycodes';

@Component({
  selector: 'app-search-products',
  templateUrl: './search-products.component.html',
  styleUrls: ['./search-products.component.css']
})


export class SearchProductsComponent {
  // model: any;

  // searching = false;
  // searchFailed = false;


  // constructor(private itemService: ItemlistService) { }

  //ngOnInit() {
  //   this.itemService.getItemList()
  //   .subscribe(products => {
  //     this.products = products;
  //     this.filteredProducts = this.products.products;
  // }, error => this.errorMessage = error);
  // }

//}

visible = true;
  selectable = true;
  removable = true;
  addOnBlur = false;
 public values: any;

  separatorKeysCodes = [ENTER, COMMA];

  productCtrl = new FormControl();

  filtereProducts: Observable<any[]>;

  Products = [
    '#Shoes',
    '#Sandals'
  ];

  allProducts = [
    '#Shoes',
    '#Sandals',
  ];

  @ViewChild('productInput' , {static: false}) productInput: ElementRef;

  constructor() {
    this.filtereProducts = this.productCtrl.valueChanges.pipe(
        startWith(null),
        map((products: string | null) => products ? this.filter(products) : this.allProducts.slice()));
  }

  add(event: MatChipInputEvent): void {
    const input = event.input;
    const value = event.value;

    // Add our products
    if ((value || '').trim()) {
      this.Products.push(value.trim());
    }

    // Reset the input value
    if (input) {
      input.value = '';
    }

    this.productCtrl.setValue(null);
  }

  remove(products: any): void {
    const index = this.Products.indexOf(products);

    if (index >= 0) {
      this.Products.splice(index, 1);
    }
  }

  filter(name: string) {
    return this.allProducts.filter(products =>
        products.toLowerCase().indexOf(name.toLowerCase()) === 0);
  }

  selected(event: MatAutocompleteSelectedEvent): void {
    alert("hhh11");
    this.Products.push(event.option.viewValue);
    this.productInput.nativeElement.value = '';
    this.productCtrl.setValue(null);
  }

  onKey(event: any) { // without type info
    this.values += event.target.value + ' | ';
    alert(event);
  }

  onFoodSelection1() {
    alert("hhh");
  }





}
